header {
  color: var(--color-white);
  min-height: 216px;
  background: url("main-header-bg-desktop.6b9cdd27.jpg") 50% / cover;
  padding: 40px 32px 0;
}

header#main-header {
  background: url("main-header-bg-desktop.6b9cdd27.jpg") 50% / cover;
}

header#library-header {
  background: url("library-header-bg-desktop.9080dd1b.jpg") 50% / cover;
}

nav {
  max-width: 1280px;
  height: 32px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  display: flex;
}

#logo {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

#logo:hover {
  opacity: .8;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-2px);
}

#logo a {
  color: var(--color-white);
  font-family: var(--font-family);
  text-align: center;
  align-items: center;
  gap: 10px;
  font-size: 30px;
  font-weight: 500;
  line-height: 0;
  text-decoration: none;
  display: flex;
}

#logo img {
  width: 24px;
  height: 24px;
}

ul#nav-menu {
  text-transform: uppercase;
  gap: 20px;
  list-style: none;
  display: flex;
}

ul#nav-menu li a {
  font-family: var(--font-family);
  text-transform: uppercase;
  text-align: center;
  color: var(--color-white);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.link-menu {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.link-menu:hover, .link-menu:active, .link-menu:focus {
  opacity: .8;
  color: var(--color-red);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.link-menu:after {
  content: "";
  height: 3px;
  width: 0;
  background: none;
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.link-menu:hover:after, .link-menu.active-nav:after {
  width: 100%;
  background: var(--color-red);
}

.active-link-menu:after {
  content: "";
  height: 3px;
  width: 100%;
  background: var(--color-red);
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.search-form {
  text-align: center;
  height: 20px;
  width: 394px;
  margin: 40px auto 0;
  position: relative;
}

.search-form input[type="text"] {
  width: 100%;
  background-color: unset;
  color: var(--color-white);
  border: none;
  border-bottom: .5px var(--color-white) solid;
  font-size: 1em;
  font-family: var(--font-family);
  border-radius: 0;
  margin-right: 10px;
  padding: 0 0 5px;
  font-size: 14px;
  font-weight: 400;
}

.search-form input::-ms-input-placeholder {
  font-family: var(--font-family);
  color: var(--color-white);
  font-size: 14px;
  font-weight: 400;
}

.search-form input::placeholder {
  font-family: var(--font-family);
  color: var(--color-white);
  font-size: 14px;
  font-weight: 400;
}

.search-form button {
  background: unset;
  border: none;
  padding: 0;
  position: absolute;
  top: 2px;
  right: 0;
}

.search-form button:hover {
  cursor: pointer;
}

.warning {
  font-family: var(--font-family);
  text-align: center;
  color: var(--color-red);
  white-space: nowrap;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  top: 45px;
  left: 50%;
  transform: translateX(-50%);
}

.btns-container {
  justify-content: center;
  gap: 16px;
  margin-top: 40px;
  display: flex;
}

.btns-container button {
  font-family: var(--font-family);
  text-transform: uppercase;
  text-align: center;
  color: var(--color-white);
  width: 148px;
  height: 42px;
  background: unset;
  border: 1px var(--color-white) solid;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 133%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.library-btn {
  background: unset;
  border: 1px var(--color-white) solid;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btns-container button.library-btn-active, .watch-btn:hover, .queue-btn:hover {
  background: var(--color-orange);
  border: 1px var(--color-orange) solid;
  cursor: pointer;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 8px 43px rgba(255, 107, 1, .6);
}

@media only screen and (max-width: 768px) {
  header#main-header {
    background: url("main-header-bg-tablet.09c26cd6.jpg") 50% / cover;
  }

  header#library-header {
    background: url("library-header-bg-tablet.568bee19.jpg") 50% / cover;
  }

  .search-form {
    width: 336px;
  }

  .btns-container {
    gap: 32px;
  }

  .btns-container button {
    width: 152px;
    height: 44px;
  }
}

@media only screen and (max-width: 420px) {
  header {
    min-height: 230px;
    padding: 35px 20px 0;
  }

  header#main-header {
    background: url("main-header-bg-mobile.73944f9d.jpg") 50% / cover;
  }

  header#library-header {
    background: url("library-header-bg-mobile.c747a20e.jpg") 50% / cover;
  }

  nav {
    height: 24px;
  }

  #logo a span {
    display: none;
  }

  .search-form {
    max-width: 280px;
    width: 100%;
  }

  .warning {
    white-space: unset;
    max-width: 280px;
    width: 100%;
    text-align: left;
  }

  .btns-container {
    gap: 20px;
    margin-top: 60px;
  }

  .btns-container button {
    width: 129px;
    height: 44px;
  }
}

:root {
  --font-family: "Roboto", sans-serif;
  --color-red: #ff001b;
  --color-orange: #ff6b01;
  --color-white: #fff;
}

